.admin-chat-container {
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: #FFFFFF;
  border-radius: 10px;
  border: 1px solid rgb(199, 198, 198);
  max-width: 400px;
  margin: 0 auto;
  font-family: Arial, sans-serif;
  height: 520px; /* Fixed height for the chat container */
  overflow-y: auto; /* Enable vertical scrolling */
  position: relative; /* Position relative for absolute positioning of .admin-input */
}
.admin-chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
}
.admin-input {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: #FAF9F9;
}
.message {
  margin-bottom: 10px;
  max-width: 100%;
  border-radius: 8px;
  clear: both;
}
.received-message {
  float: left;
  clear: both;
}
.sent-message {
  float: right;
  clear: both;
}
.message .message-text {
  font-size: 14px;
}
.message .message-sender {
  font-size: 12px;
  color: #666;
}
.input-container {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 100%;
}
.admin-input input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 5px;
}
.admin-input button {
  background-color: #007BFF;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}
.admin-input button:hover {
  background-color: #0056B3;
}